.features {
  padding-top: 45px;
  position: relative;

  @include onTablet {
    padding-top: 0;
  }

  @include onDesktop {
    padding-top: 0;
  }

  &__title {
    margin-bottom: 52px;

    @include onTablet {
      margin-bottom: 52px;
    }

    @include onDesktop {
      margin-bottom: 24px;
    }
  }

  &__background {
    display: block;
    position: absolute;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: -2;
    background-color: #f2f2f2;

    @include onTablet {
      left: 124px;
      top: 240px;
      bottom: 83px;
    }

    @include onDesktop {
      top: 210px;
      left: 204px;
      right: 0;
    }
  }

  &__wrapper {
    @include onMobile {
      margin: 0 auto;
      max-width: 260px;
      overflow: hidden;
    }
  }

  &__cards {
    @include onMobile {
      --cards-count: 0;
      display: flex;
      flex-direction: row-reverse;
      justify-content: flex-start;
      transition: transform 0.3s;

      transform: translateX(
        calc(var(--cards-count) * 260px)
      );
    }
  }

  &__card {
    @include onMobile {
      width: 260px;
      margin-bottom: 50px;
    }

    &-title {
      margin-bottom: 20px;
      font-family: "DIN Pro", sans-serif;
      font-size: 30px;
      line-height: 37px;
      text-transform: uppercase;
      color: $c-green;

      @include onTablet {
        margin-bottom: 30px;
        font-size: 34px;
        line-height: 42px;
      }

      @include onDesktop {
        margin-bottom: 30px;
        font-size: 34px;
        line-height: 42px;
      }
    }

    &-list {
      padding-left: 15px;
    }

    &-item {
      margin-bottom: 10px;
      list-style: disc;
      font-family: "Helvetica Neue", sans-serif;
      font-size: 14px;
      line-height: 21px;
      color: $c-granit;

      @include onTablet {
        margin-bottom: 13px;
        font-size: 16px;
        line-height: 19px;
      }

      @include onDesktop {
        margin-bottom: 13px;
        font-size: 16px;
        line-height: 19px;
      }
    }

    &-1 {
      @include onMobile {
        flex-shrink: 0;
      }

      @include onTablet {
        grid-row-start: 1;
      }

      @include onDesktop {
        grid-row-start: 1;
      }
    }

    &-2 {
      @include onMobile {
        flex-shrink: 0;
      }

      @include onTablet {
        grid-row-start: 2;
        margin-top: 58px;
      }

      @include onDesktop {
        grid-row-start: 2;
        margin-top: 13px;
      }
    }

    &-3 {
      @include onMobile {
        flex-shrink: 0;
      }

      @include onTablet {
        grid-row-start: 3;
        margin-top: 78px;
      }

      @include onDesktop {
        grid-row-start: 3;
        margin-top: 129px;
      }
    }
  }

  &__nav {
    margin: 0 auto;
    width: 105px;

    @include onTablet {
      display: none;
    }

    @include onDesktop {
      display: none;
    }

    &-butons {
      margin-bottom: 10px;
    }

    &-prev {
      width: 48px;
      height: 14px;
      background-image: url("../images/prev.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      background-color: transparent;
      border: none;
      cursor: pointer;
      transition: transform 0.3s filter 0.3s;

      &:hover {
        transform: scale(1.1);
        filter: invert(27%) sepia(92%) saturate(1694%)
          hue-rotate(159deg) brightness(86%) contrast(91%);
      }
    }

    &-next {
      width: 52px;
      height: 14px;
      background-image: url("../images/next.svg");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      background-color: transparent;
      border: none;
      cursor: pointer;
      transition: transform 0.3s filter 0.3s;

      &:hover {
        transform: scale(1.1);
        filter: invert(27%) sepia(92%) saturate(1694%)
          hue-rotate(159deg) brightness(86%) contrast(91%);
      }
    }

    &-text {
      text-align: center;
    }

    &-current {
      font-family: "DIN Pro", sans-serif;
      font-size: 16px;
      line-height: 20px;

      color: $c-granit;
    }

    &-last {
      font-family: "DIN Pro", sans-serif;
      font-size: 11px;
      line-height: 14px;

      color: $c-gray-light;
    }
  }

  &__img {
    width: 100%;
    height: 222px;
    object-fit: contain;
    object-position: right;
    z-index: -1;

    @include onTablet {
      width: 586px;
      height: 379px;
      position: absolute;
      right: 0;
      top: 410px;
    }

    @include onDesktop {
      width: 786px;
      height: 509px;
      position: absolute;
      right: 0;
      top: 410px;
    }
  }

}
